
.seller {
  position: absolute;
  top: 174px;
  left: 0;
  bottom: 0;
  width: 100%;
  overflow: hidden;
}
.seller .seller-header {
    padding: 0 18px;
}
.seller .seller-header .header-top {
      position: relative;
      padding: 18px 0;
      position: relative;
}
.seller .seller-header .header-top:after {
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        border-top: 1px solid rgba(7, 17, 27, 0.1);
        width: 100%;
        content: '';
}
.seller .seller-header .header-top .name {
        margin-bottom: 8px;
        line-height: 14px;
        font-size: 14px;
        color: #07111b;
}
.seller .seller-header .header-top .sell {
        height: 18px;
        line-height: 18px;
        font-size: 0;
        color: #4d555d;
}
.seller .seller-header .header-top .sell .rank-rate {
          display: inline-block;
          vertical-align: top;
          margin: 0 12px 0 8px;
          font-size: 10px;
}
.seller .seller-header .header-top .sell .sell-count {
          display: inline-block;
          vertical-align: top;
          font-size: 10px;
}
.seller .seller-header .header-top .collect {
        position: absolute;
        top: 18px;
        right: 0;
        width: 36px;
        text-align: center;
}
.seller .seller-header .header-top .collect .fa-heart {
          display: block;
          margin-bottom: 4px;
          line-height: 24px;
          font-size: 24px;
}
.seller .seller-header .header-top .collect .fa-heart.scale {
            -webkit-animation-name: pulse;
                    animation-name: pulse;
            -webkit-animation-duration: 1s;
                    animation-duration: 1s;
            -webkit-animation-fill-mode: both;
                    animation-fill-mode: both;
}
.seller .seller-header .header-top .collect .text {
          display: block;
          line-height: 10px;
          font-size: 10px;
          color: #4d555d;
}
.seller .seller-header .header-bottom {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      padding: 18px 0;
}
.seller .seller-header .header-bottom .block {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        text-align: center;
        font-size: 10px;
        color: #07111b;
}
.seller .seller-header .header-bottom .block .text {
          display: block;
          margin-bottom: 4px;
          line-height: 10px;
          color: #93999f;
}
.seller .seller-header .header-bottom .block .price {
          display: inline-block;
          line-height: 24px;
          font-size: 24px;
          font-weight: 200px;
}
.seller .seller-header .header-bottom .delivery-price {
        border-left: 1px solid rgba(7, 17, 27, 0.1);
        border-right: 1px solid rgba(7, 17, 27, 0.1);
}
.seller .seller-bulletin {
    padding: 18px;
}
.seller .seller-bulletin .title {
      line-height: 14px;
      font-size: 14px;
      color: #07111b;
}
.seller .seller-bulletin .bulletin {
      padding: 8px 12px 0;
      line-height: 24px;
      font-size: 12px;
      font-weight: 200;
      color: #f01414;
}
.seller .seller-supports {
    padding: 0 18px;
}
.seller .seller-supports .support-item {
      padding: 16px 12px;
      border-top: 1px solid rgba(7, 17, 27, 0.1);
      font-size: 0;
}
.seller .seller-supports .support-item .icon {
        display: inline-block;
        width: 16px;
        height: 16px;
        vertical-align: top;
        margin-right: 6px;
        background-size: 16px 16px;
        background-repeat: no-repeat;
}
.seller .seller-supports .support-item .icon.decrease {
          background-image: url("decrease_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.seller .seller-supports .support-item .icon.decrease {
              background-image: url("decrease_4@3x.png");
}
}
.seller .seller-supports .support-item .icon.discount {
          background-image: url("discount_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.seller .seller-supports .support-item .icon.discount {
              background-image: url("discount_4@3x.png");
}
}
.seller .seller-supports .support-item .icon.guarantee {
          background-image: url("guarantee_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.seller .seller-supports .support-item .icon.guarantee {
              background-image: url("guarantee_4@3x.png");
}
}
.seller .seller-supports .support-item .icon.invoice {
          background-image: url("invoice_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.seller .seller-supports .support-item .icon.invoice {
              background-image: url("invoice_4@3x.png");
}
}
.seller .seller-supports .support-item .icon.special {
          background-image: url("special_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.seller .seller-supports .support-item .icon.special {
              background-image: url("special_4@3x.png");
}
}
.seller .seller-supports .support-item .text {
        line-height: 16px;
        font-weight: 200;
        font-size: 12px;
        color: #07111b;
}
.seller .seller-pic {
    padding: 18px;
}
.seller .seller-pic .title {
      margin-bottom: 12px;
      line-height: 14px;
      font-size: 14px;
      color: #07111b;
}
.seller .seller-pic .pic-wrapper {
      width: 100%;
      overflow: hidden;
      white-space: nowrap;
}
.seller .seller-pic .pic-wrapper .pic-list {
        font-size: 0;
}
.seller .seller-pic .pic-wrapper .pic-list .pic-item {
          display: inline-block;
          margin-right: 6px;
          width: 120px;
          height: 90px;
}
.seller .seller-pic .pic-wrapper .pic-list .pic-item:last-child {
            margin-right: 0;
}
.seller .seller-infor {
    padding: 18px 18px 0;
}
.seller .seller-infor .title {
      margin-bottom: 12px;
      line-height: 14px;
      font-size: 14px;
      color: #07111b;
}
.seller .seller-infor .infor {
      padding: 16px 12px;
      line-height: 16px;
      border-top: 1px solid rgba(7, 17, 27, 0.1);
      font-size: 12px;
      font-weight: 200;
      color: #07111b;
}
.look-pic {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  height: 100%;
  overflow: auto;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  background: rgba(7, 17, 27, 0.8);
}
.look-pic .pic-wrapper {
    width: 100%;
    min-height: 100%;
}
.look-pic .pic-wrapper .pic-main {
      margin-top: 64px;
      padding-bottom: 64px;
}
.look-pic .pic-wrapper .pic-main .pic {
        margin-top: 80px;
}
.look-pic .pic-wrapper .pic-main .pic-tip {
        height: 25px;
        line-height: 25px;
        padding-right: 20px;
        text-align: right;
        font-size: 18px;
        color: rgba(255, 255, 255, 0.8);
}
.look-pic .pic-btn {
    position: relative;
    margin-top: -64px;
    clear: both;
    text-align: center;
    width: 100%;
    font-size: 28px;
    color: rgba(255, 255, 255, 0.8);
}
.look-pic .pic-btn .fa {
      display: inline-block;
      padding: 5px;
}
.look-pic .pic-btn .fa-close {
      margin: 0 20px;
}
.look-pic.fade-enter, .look-pic.fade-leave {
    opacity: 0.5;
    background: rgba(7, 17, 27, 0.4);
}
.look-pic.fade-enter-active, .look-pic.fade-leave-active {
    opacity: 0;
    background: rgba(7, 17, 27, 0);
}
@-webkit-keyframes pulse {
from {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
50% {
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
            transform: scale3d(1.05, 1.05, 1.05);
}
to {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
}
@keyframes pulse {
from {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
50% {
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
            transform: scale3d(1.05, 1.05, 1.05);
}
to {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
}



