
.shopcart {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 50;
  width: 100%;
  height: 48px;
  line-height: 48px;
}
.shopcart .content {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background: #141d27;
    font-size: 0;
    color: rgba(255, 255, 255, 0.4);
}
.shopcart .content .content-left {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
.shopcart .content .content-left .logo-wrapper {
        display: inline-block;
        position: relative;
        top: -10px;
        margin: 0 12px;
        padding: 6px;
        width: 56px;
        height: 56px;
        box-sizing: border-box;
        vertical-align: top;
        border-radius: 50%;
        background: #141d27;
}
.shopcart .content .content-left .logo-wrapper .logo {
          width: 100%;
          height: 100%;
          border-radius: 50%;
          text-align: center;
          background: #2b343c;
}
.shopcart .content .content-left .logo-wrapper .logo.highlight {
            background: #00a0dc;
}
.shopcart .content .content-left .logo-wrapper .logo .fa-shopping-cart {
            line-height: 44px;
            font-size: 24px;
}
.shopcart .content .content-left .logo-wrapper .logo .fa-shopping-cart.highlight {
              color: #fff;
}
.shopcart .content .content-left .logo-wrapper .num {
          position: absolute;
          top: 0;
          right: 0;
          width: 24px;
          height: 16px;
          line-height: 16px;
          text-align: center;
          border-radius: 16px;
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
          font-size: 9px;
          font-weight: 700;
          color: #fff;
          background-color: #f01414;
}
@media only screen and (max-width: 320px) {
.shopcart .content .content-left .logo-wrapper {
          margin: 0 6px;
}
}
.shopcart .content .content-left .price {
        display: inline-block;
        line-height: 24px;
        vertical-align: middle;
        padding-right: 12px;
        box-sizing: border-box;
        border-right: 1px solid rgba(255, 255, 255, 0.1);
        font-size: 16px;
        font-weight: 700;
}
.shopcart .content .content-left .price.highlight {
          color: #fff;
}
.shopcart .content .content-left .desc {
        display: inline-block;
        line-height: 24px;
        vertical-align: middle;
        padding-left: 12px;
        box-sizing: border-box;
        font-size: 10px;
        font-weight: 700;
}
.shopcart .content .content-right {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 105px;
              flex: 0 0 105px;
      width: 105px;
}
.shopcart .content .content-right .pay {
        height: 48px;
        line-height: 48px;
        padding: 0 8px;
        text-align: center;
        font-size: 12px;
        font-weight: 700;
        background: #2b343c;
}
.shopcart .content .content-right .pay.enough {
          background: #00b43c;
          color: #fff;
}
@media only screen and (max-width: 320px) {
.shopcart .content .content-right {
        -webkit-box-flex: 0;
            -ms-flex: 0 0 85px;
                flex: 0 0 85px;
        width: 85px;
}
}
.shopcart .shopcart-list {
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: 100%;
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
}
.shopcart .shopcart-list.fold-enter-active, .shopcart .shopcart-list.fold-leave-active {
      -webkit-transition: all 0.5s;
      transition: all 0.5s;
      -webkit-transform: translate3d(0, -100%, 0);
              transform: translate3d(0, -100%, 0);
}
.shopcart .shopcart-list.fold-enter, .shopcart .shopcart-list.fold-leave-active {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
}
.shopcart .shopcart-list .list-header {
      height: 40px;
      line-height: 40px;
      padding: 0 18px;
      background-color: #f3f5f7;
      border-bottom: 2px solid rgba(7, 17, 27, 0.1);
}
.shopcart .shopcart-list .list-header .title {
        float: left;
        font-size: 14px;
        font-weight: 200;
        color: #07111b;
}
.shopcart .shopcart-list .list-header .empty {
        float: right;
        font-size: 12px;
        color: #00a0dc;
}
.shopcart .shopcart-list .list-header .empty i {
          display: inline-block;
          margin-right: 4px;
          color: #93999f;
}
.shopcart .shopcart-list .list-content {
      padding: 0 18px;
      max-height: 217px;
      background-color: #fff;
      overflow: hidden;
}
.shopcart .shopcart-list .list-content .food {
        position: relative;
        padding: 12px 0;
        box-sizing: border-box;
        background-color: #fff;
        position: relative;
}
.shopcart .shopcart-list .list-content .food:after {
          display: block;
          position: absolute;
          left: 0;
          bottom: 0;
          border-top: 1px solid rgba(7, 17, 27, 0.1);
          width: 100%;
          content: '';
}
.shopcart .shopcart-list .list-content .food .name {
          line-height: 24px;
          font-size: 14px;
          color: #07111b;
}
.shopcart .shopcart-list .list-content .food .price {
          position: absolute;
          right: 90px;
          bottom: 24px;
          line-height: 24px;
          margin: 0 6px 0 18px;
          font-size: 10px;
          font-weight: normal;
          color: #f01414;
}
.shopcart .shopcart-list .list-content .food .price span {
            font-size: 14px;
            font-weight: 700;
}
.shopcart .shopcart-list .list-content .food .cartcontrol-wrapper {
          position: absolute;
          right: 0;
          bottom: 4px;
}
.shopcart .cart-mask {
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: -2;
    width: 100%;
    height: 100%;
    opacity: 1;
    background: rgba(7, 17, 27, 0.6);
}
.shopcart .cart-mask.fade-enter-active, .shopcart .cart-mask.fade-leave-active {
      opacity: 1;
      -webkit-transition: all 0.5s;
      transition: all 0.5s;
      background: rgba(7, 17, 27, 0.6);
}
.shopcart .cart-mask.fade-enter, .shopcart .cart-mask.fade-leave-active {
      opacity: 0;
      background: rgba(7, 17, 27, 0);
}



