
.ratingselect {
  position: relative;
  padding: 18px 18px 12px;
  position: relative;
}
.ratingselect:after {
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    border-bottom: 2px solid rgba(7, 17, 27, 0.1);
    width: 100%;
    content: '';
}
.ratingselect .rating-type {
    padding-bottom: 18px;
    position: relative;
    font-size: 12px;
}
.ratingselect .rating-type:after {
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      border-top: 1px solid rgba(7, 17, 27, 0.1);
      width: 100%;
      content: '';
}
.ratingselect .rating-type .block {
      display: inline-block;
      margin-right: 8px;
      height: 16px;
      line-height: 16px;
      border-radius: 2px;
      text-align: center;
      padding: 8px 12px;
}
.ratingselect .rating-type .block .count {
        margin-left: 2px;
        font-size: 8px;
}
.ratingselect .rating-type .block.active {
        color: #fff;
}
.ratingselect .rating-type .positive {
      color: #4d555d;
      background-color: rgba(0, 160, 220, 0.2);
}
.ratingselect .rating-type .positive.active {
        background-color: #00a0dc;
}
.ratingselect .rating-type .negative {
      color: #4d555d;
      background-color: rgba(77, 85, 93, 0.2);
}
.ratingselect .rating-type .negative.active {
        background-color: #4d555d;
}
.ratingselect .rating-switch {
    display: inline-block;
    height: 24px;
    line-height: 24px;
    padding-top: 12px;
    color: #93999f;
    font-size: 0;
}
.ratingselect .rating-switch .fa-check-circle {
      font-size: 24px;
}
.ratingselect .rating-switch .fa-check-circle.on {
        color: #00c850;
}
.ratingselect .rating-switch .text {
      display: inline-block;
      vertical-align: top;
      margin-left: 4px;
      font-size: 12px;
}



