
.food {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 48px;
  z-index: 1;
  width: 100%;
  background-color: #fff;
}
.food.move-enter-active, .food.move-leave-active {
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
.food.move-enter, .food.move-leave-active {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
.food .food-content .image-header {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%;
}
.food .food-content .image-header img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
}
.food .food-content .image-header .back {
      position: fixed;
      top: 5px;
      left: 0;
      z-index: 100;
      color: #07111b;
}
.food .food-content .image-header .back .fa-angle-left {
        display: inline-block;
        padding: 10px 5px 10px 10px;
        font-size: 24px;
}
.food .food-content .image-header .back .text {
        display: inline-block;
        vertical-align: middle;
        padding: 5px 10px 10px 0;
        font-size: 14px;
}
.food .food-content .image-header .back:active {
        color: #f01414;
}
.food .food-content .content {
    position: relative;
    padding: 18px;
    background-color: #fff;
}
.food .food-content .content .title {
      height: 14px;
      line-height: 14px;
      font-size: 14px;
      font-weight: 700;
      color: #07111b;
}
.food .food-content .content .detail {
      margin: 8px 0 18px 0;
      height: 10px;
      line-height: 10px;
      font-size: 10px;
      color: #93999f;
}
.food .food-content .content .detail .sell-count {
        margin-right: 12px;
}
.food .food-content .content .price {
      height: 24px;
      line-height: 24px;
      vertical-align: bottom;
      font-size: 10px;
      font-weight: normal;
      color: #f01414;
}
.food .food-content .content .price .now {
        margin-right: 12px;
        font-size: 14px;
        font-weight: 700;
}
.food .food-content .content .price .old {
        font-size: 10px;
        color: #93999f;
}
.food .food-content .content .add-to-cart {
      position: absolute;
      right: 18px;
      bottom: 18px;
      width: 74px;
      height: 24px;
      line-height: 24px;
      text-align: center;
      border-radius: 12px;
      font-size: 10px;
      color: #fff;
      background-color: #00a0dc;
}
.food .food-content .content .add-to-cart.fade-enter-active, .food .food-content .content .add-to-cart.fade-leave-active {
        -webkit-transition: all 0.6s;
        transition: all 0.6s;
}
.food .food-content .content .add-to-cart.fade-enter, .food .food-content .content .add-to-cart.fade-leave-active {
        opacity: 0;
}
.food .food-content .content .cartcontrol-wrapper {
      position: absolute;
      right: 12px;
      bottom: 12px;
}
.food .food-content .desc {
    padding: 18px;
    background-color: #fff;
}
.food .food-content .desc .title {
      height: 14px;
      line-height: 14px;
      font-size: 14px;
      font-weight: 700;
      color: #07111b;
}
.food .food-content .desc .info {
      padding: 6px 8px;
      line-height: 24px;
      font-size: 12px;
      font-weight: 200;
      color: #4d555d;
}
.food .food-content .food-rating {
    background-color: #fff;
}
.food .food-content .food-rating .title {
      height: 14px;
      line-height: 14px;
      padding: 18px 18px 0;
      font-size: 14px;
      font-weight: 700;
      color: #07111b;
}
.food .food-content .food-rating .rating-warpper {
      padding: 0 18px;
}
.food .food-content .food-rating .rating-warpper .rating-item {
        position: relative;
        padding: 16px 0;
        position: relative;
}
.food .food-content .food-rating .rating-warpper .rating-item:after {
          display: block;
          position: absolute;
          left: 0;
          bottom: 0;
          border-top: 1px solid rgba(7, 17, 27, 0.1);
          width: 100%;
          content: '';
}
.food .food-content .food-rating .rating-warpper .rating-item .user {
          position: absolute;
          top: 16px;
          right: 0;
          font-size: 0;
}
.food .food-content .food-rating .rating-warpper .rating-item .user .name {
            display: inline-block;
            margin-right: 6px;
            vertical-align: top;
            height: 12px;
            line-height: 12px;
            font-size: 10px;
            color: #93999f;
}
.food .food-content .food-rating .rating-warpper .rating-item .user .avatar {
            border-radius: 50%;
}
.food .food-content .food-rating .rating-warpper .rating-item .time {
          height: 12px;
          line-height: 12px;
          margin-bottom: 6px;
          font-size: 10px;
          color: #93999f;
}
.food .food-content .food-rating .rating-warpper .rating-item .text {
          line-height: 16px;
          font-size: 12px;
          color: #07111b;
}
.food .food-content .food-rating .rating-warpper .rating-item .text .fa {
            display: inline-block;
            margin-right: 4px;
            height: 16px;
            line-height: 16px;
}
.food .food-content .food-rating .rating-warpper .rating-item .text .fa-thumbs-up {
            color: #00a0dc;
}
.food .food-content .food-rating .rating-warpper .rating-item .text .fa-thumbs-down {
            color: #93999f;
}
.food .food-content .food-rating .rating-warpper .rating-item:last-child:after {
        display: none;
}
.food .food-content .food-rating .rating-warpper .no-rating {
        padding: 16px 0;
        font-size: 12px;
        color: #93999f;
}



