
.header {
  position: relative;
  overflow: hidden;
  color: #fff;
  background: rgba(7, 17, 27, 0.5);
}
.header .content-wrapper {
    position: relative;
    padding: 24px 12px 18px 24px;
    font-size: 0;
}
.header .content-wrapper .avatar {
      display: inline-block;
      vertical-align: top;
}
.header .content-wrapper .avatar img {
        border-radius: 2px;
}
.header .content-wrapper .content {
      display: inline-block;
      margin-left: 16px;
      font-weight: 200;
}
.header .content-wrapper .content .title {
        margin: 2px 0 8px 0;
        font-size: 0;
}
.header .content-wrapper .content .title .brand {
          display: inline-block;
          vertical-align: top;
          width: 30px;
          height: 18px;
          background-image: url("brand@2x.png");
          background-size: 30px 18px;
          background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.header .content-wrapper .content .title .brand {
              background-image: url("brand@3x.png");
}
}
.header .content-wrapper .content .title .name {
          margin-left: 6px;
          font-size: 16px;
          line-height: 18px;
          font-weight: bold;
}
.header .content-wrapper .content .description {
        margin-bottom: 10px;
        line-height: 12px;
        font-size: 12px;
}
.header .content-wrapper .content .support .icon {
        display: inline-block;
        width: 12px;
        height: 12px;
        vertical-align: top;
        margin-right: 4px;
        background-size: 12px 12px;
        background-repeat: no-repeat;
}
.header .content-wrapper .content .support .icon.decrease {
          background-image: url("decrease_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.header .content-wrapper .content .support .icon.decrease {
              background-image: url("decrease_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.discount {
          background-image: url("discount_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.header .content-wrapper .content .support .icon.discount {
              background-image: url("discount_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.guarantee {
          background-image: url("guarantee_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.header .content-wrapper .content .support .icon.guarantee {
              background-image: url("guarantee_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.invoice {
          background-image: url("invoice_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.header .content-wrapper .content .support .icon.invoice {
              background-image: url("invoice_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.special {
          background-image: url("special_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.header .content-wrapper .content .support .icon.special {
              background-image: url("special_1@3x.png");
}
}
.header .content-wrapper .content .support .text {
        height: 12px;
        line-height: 12px;
        font-size: 10px;
}
.header .content-wrapper .content .support-count {
        position: absolute;
        bottom: 14px;
        right: 12px;
        padding: 5px 10px;
        height: 24px;
        line-height: 24px;
        border-radius: 12px;
        background: rgba(0, 0, 0, 0.2);
        text-align: center;
}
.header .content-wrapper .content .support-count .count {
          display: inline-block;
          margin-right: 4px;
          vertical-align: top;
          font-size: 10px;
}
.header .content-wrapper .content .support-count .fa-angle-right {
          font-size: 10px;
          vertical-align: middle;
}
.header .bulletin-wrapper {
    position: relative;
    height: 28px;
    line-height: 28px;
    padding: 0 22px 0 12px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    background: rgba(7, 17, 27, 0.2);
}
.header .bulletin-wrapper .bulletin-title {
      display: inline-block;
      vertical-align: middle;
      width: 22px;
      height: 12px;
      background-image: url("bulletin@2x.png");
      background-size: 22px 12px;
      background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.header .bulletin-wrapper .bulletin-title {
          background-image: url("bulletin@3x.png");
}
}
.header .bulletin-wrapper .bulletin-text {
      vertical-align: middle;
      margin: 0 4px;
      font-size: 10px;
}
.header .bulletin-wrapper .fa-angle-right {
      position: absolute;
      font-size: 10px;
      top: 10px;
      right: 12px;
}
.header .background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}
.header .detail {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    width: 100%;
    height: 100%;
    overflow: auto;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    background: rgba(7, 17, 27, 0.8);
}
.header .detail.fade-enter, .header .detail.fade-leave {
      opacity: 0.5;
      background: rgba(7, 17, 27, 0.4);
}
.header .detail.fade-enter-active, .header .detail.fade-leave-active {
      opacity: 0;
      background: rgba(7, 17, 27, 0);
}
.header .detail .detail-wrapper {
      width: 100%;
      min-height: 100%;
}
.header .detail .detail-wrapper .detail-main {
        margin-top: 64px;
        padding-bottom: 64px;
}
.header .detail .detail-wrapper .detail-main .name {
          line-height: 16px;
          text-align: center;
          font-size: 16px;
          font-weight: 700;
}
.header .detail .detail-wrapper .detail-main .star-wrapper {
          margin-top: 18px;
          padding: 2px 0;
          text-align: center;
}
.header .detail .detail-wrapper .detail-main .title {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          width: 80%;
          margin: 28px auto 24px auto;
}
.header .detail .detail-wrapper .detail-main .title .line {
            -webkit-box-flex: 1;
                -ms-flex: 1;
                    flex: 1;
            position: relative;
            top: -6px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
.header .detail .detail-wrapper .detail-main .title .text {
            padding: 0 12px;
            font-weight: 700;
            font-size: 14px;
}
.header .detail .detail-wrapper .detail-main .supports {
          width: 80%;
          margin: 0 auto;
}
.header .detail .detail-wrapper .detail-main .supports .support-item {
            padding: 0 12px;
