.header .detail .detail-wrapper .detail-main .supports .support-item .icon.guarantee {
                background-image: url("guarantee_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.guarantee {
                    background-image: url("guarantee_2@3x.png");
}
}
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.invoice {
                background-image: url("invoice_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.invoice {
                    background-image: url("invoice_2@3x.png");
}
}
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.special {
                background-image: url("special_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.special {
                    background-image: url("special_2@3x.png");
}
}
.header .detail .detail-wrapper .detail-main .supports .support-item .text {
              line-height: 16px;
              font-weight: 200;
              font-size: 12px;
}
.header .detail .detail-wrapper .detail-main .bulletin {
          width: 80%;
          margin: 0 auto;
}
.header .detail .detail-wrapper .detail-main .bulletin .content {
            padding: 0 12px;
            line-height: 24px;
            font-size: 12px;
}
.header .detail .detail-close {
      position: relative;
      width: 32px;
      height: 32px;
      margin: -64px auto 0 auto;
      clear: both;
      font-size: 32px;
}
.header .look-avatar {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    width: 100%;
    height: 100%;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    background: rgba(7, 17, 27, 0.8);
}
.header .look-avatar.fade-enter, .header .look-avatar.fade-leave {
      opacity: 0.5;
      background: rgba(7, 17, 27, 0.4);
}
.header .look-avatar.fade-enter-active, .header .look-avatar.fade-leave-active {
      opacity: 0;
      background: rgba(7, 17, 27, 0);
}
.header .look-avatar .avatar {
      position: relative;
      max-height: 100%;
      max-width: 100%;
      width: auto;
      height: auto;
      top: 50%;
      left: 50%;
      -webkit-transform: translateX(-50%) translateY(-50%);
      transform: translateX(-50%) translateY(-50%);
}



