
.goods {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 174px;
  bottom: 46px;
  width: 100%;
  overflow: hidden;
}
.goods .meau-wrapper {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 80px;
            flex: 0 0 80px;
    width: 80px;
    background: #f3f5f7;
}
.goods .meau-wrapper .meau-item {
      display: table;
      width: 56px;
      height: 54px;
      padding: 0 12px;
      line-height: 14px;
}
.goods .meau-wrapper .meau-item .icon {
        display: inline-block;
        width: 14px;
        height: 14px;
        vertical-align: top;
        margin-right: 2px;
        background-size: 14px 14px;
        background-repeat: no-repeat;
}
.goods .meau-wrapper .meau-item .icon.decrease {
          background-image: url("decrease_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.goods .meau-wrapper .meau-item .icon.decrease {
              background-image: url("decrease_3@3x.png");
}
}
.goods .meau-wrapper .meau-item .icon.discount {
          background-image: url("discount_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.goods .meau-wrapper .meau-item .icon.discount {
              background-image: url("discount_3@3x.png");
}
}
.goods .meau-wrapper .meau-item .icon.guarantee {
          background-image: url("guarantee_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.goods .meau-wrapper .meau-item .icon.guarantee {
              background-image: url("guarantee_3@3x.png");
}
}
.goods .meau-wrapper .meau-item .icon.invoice {
          background-image: url("invoice_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.goods .meau-wrapper .meau-item .icon.invoice {
              background-image: url("invoice_3@3x.png");
}
}
.goods .meau-wrapper .meau-item .icon.special {
          background-image: url("special_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
.goods .meau-wrapper .meau-item .icon.special {
              background-image: url("special_3@3x.png");
}
}
.goods .meau-wrapper .meau-item .text {
        display: table-cell;
        width: 56px;
        vertical-align: middle;
        text-align: center;
        position: relative;
        font-size: 12px;
}
.goods .meau-wrapper .meau-item .text:after {
          display: block;
          position: absolute;
          left: 0;
          bottom: 0;
          border-top: 1px solid rgba(7, 17, 27, 0.1);
          width: 100%;
          content: '';
}
.goods .meau-wrapper .meau-item:last-child .text:after {
        display: none;
}
.goods .meau-wrapper .meau-item.current {
        position: relative;
        z-index: 10;
        margin-top: -1px;
        background: #fff;
        font-weight: 700;
}
.goods .meau-wrapper .meau-item.current .text:after {
          display: none;
}
@media only screen and (max-width: 320px) {
.goods .meau-wrapper {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 60px;
              flex: 0 0 60px;
      width: 60px;
}
.goods .meau-wrapper .meau-item {
        width: 44px;
        height: 44px;
        padding: 0 8px;
}
}
.goods .foods-wrapper {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    background: #fff;
}
.goods .foods-wrapper .title {
      height: 26px;
      line-height: 26px;
      padding-left: 14px;
      border-left: 2px solid #d9dde1;
      font-size: 12px;
      font-weight: 700;
      color: #93999f;
      background-color: #f3f5f7;
}
.goods .foods-wrapper .food-item {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      margin: 18px;
      padding-bottom: 18px;
      position: relative;
}
.goods .foods-wrapper .food-item:after {
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        border-top: 1px solid rgba(7, 17, 27, 0.1);
        width: 100%;
        content: '';
}
.goods .foods-wrapper .food-item:last-child {
        margin-bottom: 0;
}
.goods .foods-wrapper .food-item:last-child:after {
          display: none;
}
.goods .foods-wrapper .food-item .icon {
        -webkit-box-flex: 0;
            -ms-flex: 0 0 57px;
                flex: 0 0 57px;
        width: 57px;
        margin-right: 10px;
}
.goods .foods-wrapper .food-item .content {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
}
.goods .foods-wrapper .food-item .content .name {
          margin: 2px 0 8px;
          line-height: 14px;
          font-size: 14px;
          color: #07111b;
}
.goods .foods-wrapper .food-item .content .desc,
        .goods .foods-wrapper .food-item .content .extra {
          line-height: 10px;
          font-size: 10px;
          color: #93999f;
}
.goods .foods-wrapper .food-item .content .extra {
          margin-top: 8px;
}
.goods .foods-wrapper .food-item .content .extra .rating {
            display: inline-block;
            margin-left: 12px;
}
.goods .foods-wrapper .food-item .content .price {
          height: 24px;
          line-height: 24px;
          font-weight: 700;
}
.goods .foods-wrapper .food-item .content .price .now {
            display: inline-block;
            vertical-align: middle;
            margin-right: 8px;
            font-size: 14px;
            color: #f01414;
}
.goods .foods-wrapper .food-item .content .price .old {
            display: inline-block;
            vertical-align: middle;
            font-size: 10px;
            color: #93999f;
}
.goods .foods-wrapper .food-item .content .cartcontrol-wrapper {
          position: absolute;
          right: 0;
          bottom: 12px;
}
@media only screen and (max-width: 320px) {
.goods .foods-wrapper .food-item {
        margin: 10px;
        padding-bottom: 10px;
}
.goods .foods-wrapper .food-item .content .cartcontrol-wrapper {
          right: -6px;
          bottom: 8px;
}
}



